import { createContext, useContext, useEffect, useState } from "react";
import { useToast } from "@chakra-ui/react";
import dayjs from "dayjs";
import { ClientJS } from "clientjs";
import { v5 as uuidv5 } from "uuid";
import grassApi from "../apis/grassApi";
import { Device, Profile, Role, Settings } from "../models/user";
import useLocalStorage from "../hooks/useLocalStorage";
import { useNavigate } from "react-router-dom";
import { ROUTES } from "../constants/routes";
import { Actions, Languages, Status } from "../models/settings";
import {
  AUTHENTICATED_KEY,
  BROWSER_ID_KEY,
  DEVICE_KEY,
  PROFILE_KEY,
  SETTINGS_KEY,
  STATUS_KEY,
  USER_ID_KEY,
} from "../constants/keys";
import { handleFusionauthError } from "../utils/fusionauth";
import { getIpAddress } from "../utils/ip";
import { getStorageValue } from "../utils/storage";

interface AuthContextProps {
  isAuthenticated?: boolean;
  profile: Profile | null;
  device: Device | null;
  settings: Settings | null;
  guestEmail: string;
  previousScore: number;
  userId?: string;
  loginUser: (user: string, password: string) => void;
  registerUser: (
    password: string,
    email?: string,
    username?: string,
    referral?: string
  ) => void;
  logoutUser: () => Promise<void>;
  updateSettings: (
    action: Actions,
    pauseExpiry?: number,
    language?: Languages
  ) => void;
  reconnect: () => void;
}

export const AuthContext = createContext<AuthContextProps>({
  profile: null,
  device: null,
  settings: null,
  guestEmail: "",
  previousScore: 0,
  userId: "",
  loginUser: () => {},
  registerUser: () => {},
  logoutUser: async () => {},
  updateSettings: () => {},
  reconnect: () => {},
});

interface AuthProviderProps {
  children: React.ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const toast = useToast();
  const navigate = useNavigate();
  const [isAuthenticated, setIsAuthenticated] = useLocalStorage<
    boolean | undefined
  >(AUTHENTICATED_KEY, undefined);
  const [profile, setProfile] = useLocalStorage<Profile | null>(
    PROFILE_KEY,
    null
  );
  const [device, setDevice] = useLocalStorage<Device | null>(DEVICE_KEY, null);
  const [settings, setSettings] = useLocalStorage<Settings | null>(
    SETTINGS_KEY,
    null
  );
  const [browserId, setBrowserId] = useLocalStorage<string>(BROWSER_ID_KEY, "");
  const [userId, setUserId] = useLocalStorage<string>(USER_ID_KEY, "");
  const [guestEmail, setGuestEmail] = useLocalStorage<string>("guestEmail", "");
  const [previousScore, setPreviousScore] = useLocalStorage<number>(
    "previousScore",
    0
  );
  const [ipAddress, setIpAddress] = useState("");
  const [status] = useLocalStorage<Status>(STATUS_KEY, Status.DISCONNECTED);

  useEffect(() => {
    // Run this to declare a unique browser id
    const loadBrowserId = async () => {
      const browserIdStorage = await getStorageValue(BROWSER_ID_KEY);

      if (!browserIdStorage) {
        const client = new ClientJS();
        const fingerprint = client.getFingerprint();
        const uuid = uuidv5(
          fingerprint.toString(),
          process.env.REACT_APP_NAMESPACE as string
        );
        setBrowserId(uuid);

        if (
          typeof chrome.runtime !== "undefined" &&
          process.env.NODE_ENV === "production"
        ) {
          chrome.runtime.sendMessage("reconnect", (response) => {
            console.log(response);
          });
        }
      }
    };

    loadBrowserId().catch(console.error);
    // eslint-disable-next-line
  }, [isAuthenticated]);

  useEffect(() => {
    const intervalFunction = setInterval(() => {
      if (isAuthenticated) {
        loadIpAddress();
      }
    }, 10000);

    return () => {
      clearInterval(intervalFunction);
    };
  }, [isAuthenticated]);

  useEffect(() => {
    if (profile) {
      setUserId(profile.id);
    }
  }, [profile]);

  useEffect(() => {
    const loadData = async () => {
      const userIdStorage = await getStorageValue(USER_ID_KEY);
      const isAuthenticatedStorage = await getStorageValue(AUTHENTICATED_KEY);

      if (typeof userIdStorage !== "string") {
        setIsAuthenticated(false);
      } else {
        if (isAuthenticatedStorage && !profile) {
          loadProfile();
        } else if (isAuthenticatedStorage && profile && userId) {
          setIsAuthenticated(true);
          navigate(ROUTES.DASHBOARD);
        } else if (!isAuthenticatedStorage && !profile) {
          navigate(ROUTES.LOGIN);
        }
      }
    };

    loadData().catch(console.error);
  }, [userId, profile, isAuthenticated]);

  useEffect(() => {
    if (isAuthenticated && profile && browserId) {
      if (!device) {
        getCurrentDevice(profile.id);
      } else {
        if (status === Status.CONNECTED) {
          getCurrentDevice(profile.id);
        }
      }
    } else {
      setDevice(null);
    }
  }, [profile, browserId, isAuthenticated, status]);

  useEffect(() => {
    if (isAuthenticated && device) {
      if (!settings) {
        getSettings();
      } else {
        if (status === Status.CONNECTED) {
          getSettings();
        }
      }
    } else {
      setSettings(null);
    }
  }, [device, isAuthenticated, status]);

  const loadProfile = async () => {
    await grassApi
      .get(`/users`, { withCredentials: true })
      .then((res) => {
        setProfile(res.data.data.result);
      })
      .catch((err) => {
        console.error("[LOAD PROFILE ERROR]", err);
      });
  };

  const loadIpAddress = async () => {
    const loadedIpAddress = await getIpAddress();
    setIpAddress((prevState) => {
      if (Boolean(prevState) && prevState !== loadedIpAddress) {
        toast({
          title: "Network changed",
          description: "Extension will reconnect",
          status: "info",
          variant: "info",
          position: "top-left",
          isClosable: true,
        });

        if (
          typeof chrome.runtime !== "undefined" &&
          process.env.NODE_ENV === "production"
        ) {
          chrome.runtime.sendMessage("reconnect", (response) => {
            console.log(response);
          });
        }
      }

      return loadedIpAddress;
    });
  };

  const loginUser = async (user: string, password: string) => {
    const body = JSON.stringify({
      user,
      password,
    });
    await grassApi
      .post(`/auth/login`, body, { withCredentials: true })
      .then((res) => {
        setIsAuthenticated(true);
        navigate(ROUTES.DASHBOARD);
      })
      .then(() => {
        loadProfile();
      })
      .catch((err) => {
        console.error("[LOGIN ERROR]", err);

        toast({
          title: "Error",
          description: err.response.data.message,
          status: "error",
          variant: "error",
          position: "top-left",
          isClosable: true,
        });
      });
  };

  const registerUser = async (
    password: string,
    email?: string,
    username?: string,
    referral?: string
  ) => {
    let registerUserUrl = `/auth/reguser`;

    if (!email) {
      registerUserUrl = `/auth/regGuest`;
    }

    const body = JSON.stringify({
      password,
      email,
      role: Role.SELLER,
      username,
      referral,
    });

    await grassApi
      .post(registerUserUrl, body, {
        withCredentials: true,
      })
      .then(async (res) => {
        setProfile(res.data.data.profile);
        setIsAuthenticated(true);

        if (!email) {
          // Set the guest email on storage so the user can log in again with password only
          setGuestEmail(res.data.data.user.email);
        }
      })
      .catch((err) => {
        console.error("[REGISTER ERROR]", err);
        const faError = handleFusionauthError(err);

        toast({
          title: "Error",
          description:
            faError ||
            err.response.data.message ||
            err.message ||
            "Something went wrong",
          status: "error",
          variant: "error",
          position: "top-left",
          isClosable: true,
        });
      });
  };

  const logoutUser = async () => {
    await grassApi
      .post(`/auth/logout`, undefined, { withCredentials: true })
      .then(() => {
        setProfile(null);
        setIsAuthenticated(false);
        setDevice(null);
        setSettings(null);
        setUserId("");
        // navigate(ROUTES.LOGIN);
      })
      .catch((err) => {
        console.error("[LOGOUT ERROR]", err);
      });
  };

  const initializeSettings = async () => {
    const body = JSON.stringify({
      language: Languages.ENGLISH,
    });

    await grassApi
      .post(`/extension`, body, { withCredentials: true })
      .catch((err) => {
        console.error("[INITIALIZE ERROR]", err);
      });
  };

  const updateSettings = async (
    action: Actions,
    pauseExpiry?: number,
    language?: Languages
  ) => {
    const body = JSON.stringify({
      action,
      pause_expiry: pauseExpiry,
      language,
    });

    await grassApi
      .put(`/extension`, body, { withCredentials: true })
      .then(() => {
        getSettings();
        toast({
          title: "Success",
          description: "Settings have been updated",
          status: "success",
          variant: "success",
          position: "top-left",
          isClosable: true,
        });
      })
      .catch((err) => {
        console.error("[SETTINGS UPDATE ERROR]", err);
      });
  };

  const getSettings = async () => {
    await grassApi
      .get(`/extension`, { withCredentials: true })
      .then((res) => {
        if (res.data.data.user) {
          setSettings(res.data.data.user);
        } else {
          initializeSettings();
        }
      })
      .catch((err) => {
        console.error("[SETTINGS ERROR]", err);
      });
  };

  const getCurrentDevice = async (userId: string) => {
    let getDeviceUrl = `/extension/device?device_id=${browserId}&user_id=${userId}`;

    await grassApi
      .get(getDeviceUrl, { withCredentials: true })
      .then((res) => {
        const currentDevice = res.data.data;

        if (currentDevice) {
          if (device && device.device_ip !== currentDevice.device_ip) {
            setPreviousScore(device.final_score);
          }
          setDevice(currentDevice);
        }
      })
      .catch((err) => {
        console.error("[DEVICES ERROR]", err);
      });
  };

  const reconnect = async () => {
    if (
      typeof chrome.runtime !== "undefined" &&
      process.env.NODE_ENV === "production"
    ) {
      chrome.runtime.sendMessage("reconnect", (response) => {
        console.log(response);
      });
    } else {
      console.error("Cannot perform reconnection");
    }
  };

  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        profile,
        device,
        settings,
        guestEmail,
        previousScore,
        userId,
        loginUser,
        registerUser,
        logoutUser,
        updateSettings,
        reconnect,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuthContext = () => {
  return useContext(AuthContext);
};
