export const handleFusionauthError = (error: any) => {
  if (error.response.data.message.exception) {
    const fusionauthError = error.response.data.message.exception;
    const prioritizedError = Object.keys(fusionauthError)[0];

    if (prioritizedError) {
      const prioritizedErrorValues = Object.values(
        fusionauthError[prioritizedError]
      )[0];
      const errorMessage = (prioritizedErrorValues as any[])[0].message;
      return errorMessage;
    }
  }

  return "";
};
