import { Button, Heading, Icon, Stack, Progress } from "@chakra-ui/react";
import { FormProvider, useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import { useState } from "react";

import { ReactComponent as CheckCircleIcon } from "../assets/icons/check-circle-icon.svg";
import { ROUTES } from "../constants/routes";
import { useAuthContext } from "../contexts/AuthContext";
import BackButton from "../components/BackButton";
import {
  RegistrationStepOne,
  RegistrationStepTwo,
} from "../components/Forms/RegistrationForms";

const REGISTRATION_STEPS = 2;

const Registration = () => {
  const { registerUser, isAuthenticated } = useAuthContext();
  const [currentStep, setCurrentStep] = useState(1);
  const methods = useForm();

  const onSubmit = methods.handleSubmit((data) => {
    registerUser(data.password, data.email, data.username, data.referral);
  });

  return isAuthenticated ? (
    <Stack spacing="8" align="center">
      <Heading fontSize="2xl" textAlign="center" lineHeight="150%">
        Success <br /> You're earning!
      </Heading>
      <Icon as={CheckCircleIcon} w="150px" h="150px" color="green.accent.1" />
      <Link to={ROUTES.DASHBOARD} style={{ width: "100%" }}>
        <Button w="full">Go to my dashboard</Button>
      </Link>
    </Stack>
  ) : (
    <FormProvider {...methods}>
      <Stack as="form" spacing="6" textAlign="center">
        <BackButton />
        <Heading fontSize="2xl">Create your Account</Heading>
        <Progress
          variant="primary"
          borderRadius="full"
          value={(currentStep / REGISTRATION_STEPS) * 100}
          size="sm"
        />

        {currentStep === 1 ? <RegistrationStepOne /> : <RegistrationStepTwo />}
        {currentStep === 1 ? (
          <Button
            type="button"
            onClick={() => {
              if (methods.formState.isValid) {
                setCurrentStep(currentStep + 1);
              } else {
                methods.trigger();
              }
            }}
          >
            Next
          </Button>
        ) : (
          <Stack>
            <Button
              type="button"
              onClick={onSubmit}
              isLoading={methods.formState.isSubmitting}
            >
              Create Account
            </Button>
            <Button
              type="button"
              variant="outlined"
              onClick={() => {
                setCurrentStep(currentStep - 1);
              }}
            >
              Back
            </Button>
          </Stack>
        )}
      </Stack>
    </FormProvider>
  );
};

export default Registration;
