import {
  Badge,
  Box,
  Button,
  Center,
  Flex,
  Grid,
  HStack,
  Icon,
  Image,
  Link,
  Skeleton,
  Spinner,
  Stack,
  Text,
  Tooltip,
  useToast,
} from "@chakra-ui/react";
import { useEffect, useMemo, useState } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import utc from "dayjs/plugin/utc";
import dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
import _ from "lodash";
import { useNavigate } from "react-router";

import { WifiAnimation } from "../components/Wifi";

import { ReactComponent as DashboardIcon } from "../assets/icons/dashboard-icon.svg";
import { ReactComponent as LinkPlusIcon } from "../assets/icons/link-plus-icon.svg";
import { LINKS } from "../constants/links";
import { useAuthContext } from "../contexts/AuthContext";
import grassApi from "../apis/grassApi";
import useLocalStorage from "../hooks/useLocalStorage";
import {
  CURRENT_EPOCH_EARNING_KEY,
  EPOCH_EARNINGS_KEY,
  PERMISSIONS_KEY,
  STATUS_KEY,
} from "../constants/keys";
import { Actions, Status } from "../models/settings";
import { Device, EpochEarning } from "../models/user";
import tokenImage from "../assets/icons/token.png";
import { formatNumber } from "../utils/format";
import { applyFinalScore } from "../utils/seller";
import { ROUTES } from "../constants/routes";

dayjs.extend(utc);
dayjs.extend(isBetween);

const Dashboard = () => {
  const navigate = useNavigate();
  const toast = useToast();
  const {
    isAuthenticated,
    profile,
    settings,
    device,
    previousScore,
    updateSettings,
  } = useAuthContext();
  const [connectionQuality, setConnectionQuality] = useState(0);
  const [isReferralCopied, setIsReferralCopied] = useState(false);
  const [status, setStatus] = useLocalStorage(STATUS_KEY, Status.DISCONNECTED);
  const [hasPermissions] = useLocalStorage<boolean | undefined>(
    PERMISSIONS_KEY,
    undefined
  );
  const [epochEarnings, setEpochEarnings] = useLocalStorage<EpochEarning[]>(
    EPOCH_EARNINGS_KEY,
    []
  );
  const [currentEpochEarning, setCurrentEpochEarning] =
    useLocalStorage<EpochEarning | null>(CURRENT_EPOCH_EARNING_KEY, null);
  const [currentDeviceData, setCurrentDeviceData] = useState<Device[]>([]);

  const totalEpochEarnings = useMemo(() => {
    if (currentDeviceData.length > 0 && currentEpochEarning) {
      let totalUptime = currentEpochEarning.totalCumulativeUptime;
      let totalEarnings = currentEpochEarning.totalCumulativePoints;

      let totalTodayUptime = 0;
      let totalTodayEarnings = 0;

      currentDeviceData.forEach((device) => {
        let finalScore = 0;

        if (device.final_score && device.final_score >= 0) {
          finalScore = device.final_score;
        } else {
          const filteredDevices = _.filter(
            currentDeviceData,
            (item) => item.device_ip === device.device_ip
          );
          const maxFinalScore = _.maxBy(filteredDevices, "final_score");

          if (maxFinalScore) {
            finalScore = maxFinalScore?.final_score;
          }
        }

        let differenceFromLastConnected = 0;

        if (!!device.lastConnected) {
          differenceFromLastConnected = Math.abs(
            dayjs.utc().diff(dayjs.utc(device.lastConnected), "minutes")
          );

          // Greater than the difference from start of day
          const differenceFromStartOfDay = Math.abs(
            dayjs.utc().diff(dayjs.utc().startOf("day"), "minutes")
          );

          if (differenceFromLastConnected >= differenceFromStartOfDay) {
            differenceFromLastConnected = differenceFromStartOfDay;
          }
        }

        const todayUptime =
          device.totalTodayUptime + differenceFromLastConnected;
        const todayEarnings = applyFinalScore(todayUptime, finalScore);

        totalTodayUptime += todayUptime;
        totalTodayEarnings += todayEarnings;
      });

      totalUptime += totalTodayUptime;
      totalEarnings += totalTodayEarnings;

      return { totalUptime, totalEarnings };
    }
  }, [currentDeviceData, currentEpochEarning]);

  const isPaused = useMemo(() => {
    if (Boolean(settings) && Boolean(settings?.pause_proxy_expiry)) {
      return dayjs().isBefore(dayjs(settings?.pause_proxy_expiry));
    }
    return false;
  }, [settings]);
  const isConnected = useMemo(() => {
    return status === Status.CONNECTED && !isPaused && !!hasPermissions;
  }, [status, isPaused]);
  const isConnecting = useMemo(() => {
    return status === Status.CONNECTING;
  }, [status]);
  const connectionColor = useMemo(() => {
    if (isConnected) {
      if (connectionQuality >= 0 && connectionQuality <= 33) {
        return "brand.red";
      } else if (connectionQuality > 33 && connectionQuality <= 67) {
        return "brand.orange";
      } else {
        return "brand.green";
      }
    } else {
      return "brand.gray";
    }
  }, [connectionQuality, isConnected]);
  const connectionMessage = useMemo(() => {
    if (isConnected) {
      if (connectionQuality >= 0 && connectionQuality <= 33) {
        return "Poor network quality - try connecting to a different network or turning off your VPN if you have one enabled.";
      } else if (connectionQuality > 33 && connectionQuality <= 67) {
        return "Network quality is okay - you are earning at a reduced rate.";
      } else {
        return "You’re doing great! Keep connected to this network to earn.";
      }
    } else {
      if (!hasPermissions) {
        return "You have blocked permissions to access sites. Please unblock it to continue earning";
      }

      if (status === Status.DEAD) {
        return "Connection lost, trying to reconnect your device to our servers";
      } else if (status === Status.CONNECTING) {
        return "Connecting to our servers. Please wait...";
      }

      return "Connect to the internet to restart earning.";
    }
  }, [isConnected, connectionQuality, hasPermissions, status]);

  useEffect(() => {
    if (status && status === Status.DEAD && isAuthenticated) {
      toast({
        title: "Device connection loss",
        description:
          "Trying to reconnect your device to our servers. If it does not connect in a few seconds, please try refreshing the app or check your network connection.",
        status: "error",
        variant: "error",
        position: "top-left",
        isClosable: true,
      });
    }
  }, [status, isAuthenticated]);

  useEffect(() => {
    const handleNetworkEvent = (evt: Event) => {
      if (
        typeof chrome.runtime !== "undefined" &&
        process.env.NODE_ENV === "production"
      ) {
        if (evt.type === "online") {
          setStatus(Status.CONNECTED);
        } else if (evt.type === "offline") {
          setStatus(Status.DISCONNECTED);
        }
      } else {
        console.log(evt.type);
      }
    };

    window.addEventListener("online", handleNetworkEvent);
    window.addEventListener("offline", handleNetworkEvent);

    return () => {
      window.removeEventListener("online", () => {
        console.log("[ONLINE-CLEANUP]");
      });
      window.removeEventListener("offline", () => {
        console.log("[OFFLINE-CLEANUP]");
      });
    };
  }, []);

  useEffect(() => {
    let timeoutFunction: NodeJS.Timeout;

    if (isReferralCopied) {
      timeoutFunction = setTimeout(() => {
        setIsReferralCopied(false);
      }, 5000);
    }

    return () => {
      clearTimeout(timeoutFunction);
    };
  }, [isReferralCopied]);

  useEffect(() => {
    if (isAuthenticated && profile && device) {
      if (currentDeviceData.length === 0 || epochEarnings.length === 0) {
        updateCurrentDeviceData();
        loadEpochEarnings();
      } else {
        if (status === Status.CONNECTED) {
          updateCurrentDeviceData();
          loadEpochEarnings();
        }
      }
    } else if (!profile && !isAuthenticated) {
      setEpochEarnings([]);
      setCurrentEpochEarning(null);
      setCurrentDeviceData([]);
      navigate(ROUTES.LOGIN);
    }
  }, [profile, device, isAuthenticated, status]);

  useEffect(() => {
    if (device && device.final_score) {
      setConnectionQuality(device.final_score);
    } else {
      setConnectionQuality(0);
    }
  }, [device]);

  const updateCurrentDeviceData = async () => {
    try {
      const res = await grassApi.get(`/extension/user-score`, {
        withCredentials: true,
      });
      const { currentDeviceData } = res.data.data;
      setCurrentDeviceData(currentDeviceData);
    } catch (err) {
      setCurrentDeviceData([]);
    }
  };

  const loadEpochEarnings = async () => {
    await grassApi
      .get(`/users/earnings/epochs`, { withCredentials: true })
      .then((res) => {
        const { epochEarnings } = res.data.data;

        const today = dayjs.utc();
        const updatedEpochEarnings = _.map(epochEarnings, (epochEarning) => {
          const startDate = dayjs.utc(epochEarning.start_date, "YYYY-MM-DD");
          const endDate = dayjs.utc(epochEarning.end_date, "YYYY-MM-DD");

          return {
            ...epochEarning,
            isCurrentEpoch: today.isBetween(startDate, endDate, "day", "[]"),
          };
        });

        setEpochEarnings(_.reverse(updatedEpochEarnings));

        const currentEpochEarning = _.find(
          updatedEpochEarnings,
          (epochEarning) => {
            return epochEarning.isCurrentEpoch;
          }
        );
        setCurrentEpochEarning(currentEpochEarning);
      })
      .catch((err) => {
        console.error("[EPOCH EARNINGS ERROR]", err);
      });
  };

  return isConnecting ? (
    <Center mt="32">
      <Stack align="center" textAlign="center" spacing="6">
        <Spinner
          thickness="8px"
          speed="0.65s"
          emptyColor="whiteAlpha.600"
          color="green.white.400"
          w="80px"
          h="80px"
        />

        <Text fontSize="sm" color="green.white" fontWeight="600">
          Connecting ...
        </Text>
      </Stack>
    </Center>
  ) : (
    <Box>
      <Stack align="center" mt="8" spacing="6">
        <Badge
          textTransform="none"
          bgColor="whiteAlpha.100"
          borderRadius="full"
          color="white"
          w="fit-content"
          px="4"
          py="1"
        >
          <Flex align="center" gap="1.5">
            <Box
              w="2"
              h="2"
              borderRadius="full"
              bgColor={isConnected ? "brand.green" : "brand.gray"}
            />
            <Text fontSize="sm">
              {isConnected ? "Connected" : "Disconnected"}
            </Text>
          </Flex>
        </Badge>

        <Skeleton pos="relative" isLoaded={!!device}>
          <WifiAnimation
            w="20"
            h="20"
            isWorking={isConnected}
            color={connectionColor}
            pos="relative"
            zIndex="2"
          />
        </Skeleton>

        <Stack spacing="1" align="center" textAlign="center" maxW="80%">
          {isConnected && (
            <>
              <Skeleton isLoaded={!!device}>
                <Text fontSize="sm" color={connectionColor} fontWeight="600">
                  Network quality: {connectionQuality}%
                </Text>
              </Skeleton>

              <Skeleton isLoaded={!!device}>
                {!!previousScore &&
                  previousScore > 0 &&
                  device?.final_score !== previousScore && (
                    <Text fontSize="xs" fontWeight="500">
                      (previously {previousScore}%)
                    </Text>
                  )}
              </Skeleton>
            </>
          )}

          <Skeleton isLoaded={!!device}>
            <Text fontSize="sm" fontWeight="600">
              {connectionMessage}
            </Text>
          </Skeleton>
        </Stack>
      </Stack>

      {!isConnected && isPaused && (
        <Tooltip
          label={`Bandwidth is paused until ${dayjs(
            settings?.pause_proxy_expiry
          ).format("MMM DD, YYYY hh:mma ")}`}
          placement="top"
          isDisabled={!Boolean(settings?.pause_proxy_expiry)}
        >
          <Button
            w="full"
            mt="8"
            mb="-12"
            onClick={() => {
              // Cannot set to 0 because it's false as a boolean check
              updateSettings(Actions.PAUSE, 0.001);
            }}
            isLoading={!Boolean(settings)}
            isDisabled={!isPaused}
          >
            Connect
          </Button>
        </Tooltip>
      )}

      <Stack spacing="5" mt="16" align="center" textAlign="center">
        <Box w="full" h="0.7px" bgColor="whiteAlpha.200" />

        <HStack
          fontSize="sm"
          color="green.white.100"
          fontWeight="500"
          align="center"
          spacing="2"
        >
          <Skeleton isLoaded={!!currentEpochEarning}>
            <Text>{currentEpochEarning?.epoch_name} earnings: </Text>
          </Skeleton>
          <HStack spacing="1" align="center">
            <Image src={tokenImage} alt="token" w="5" h="5" />
            <Skeleton isLoaded={!!currentEpochEarning}>
              <Text>
                {totalEpochEarnings
                  ? formatNumber(totalEpochEarnings.totalEarnings)
                  : 0}
              </Text>
            </Skeleton>
          </HStack>
        </HStack>

        <Grid templateColumns="repeat(2,1fr)" gridGap="2" w="full">
          <Link href={`${LINKS.GRASS_DASHBOARD_APP}/dashboard`} isExternal>
            <Button
              variant="outlined"
              leftIcon={<Icon as={DashboardIcon} w="3" h="3" />}
              fontWeight="600"
              w="full"
            >
              Open dashboard
            </Button>
          </Link>

          <CopyToClipboard
            text={`${LINKS.GRASS_DASHBOARD_APP}/register?referralCode=${profile?.referral}`}
            onCopy={() => {
              setIsReferralCopied(true);
            }}
          >
            <Button
              variant="outlined"
              leftIcon={<Icon as={LinkPlusIcon} w="4" h="4" />}
              fontWeight="600"
              w="full"
            >
              {isReferralCopied ? "Copied" : "Refer a friend"}
            </Button>
          </CopyToClipboard>
        </Grid>
      </Stack>
    </Box>
  );
};

export default Dashboard;
