export interface UserData {
  profileId: string;
  displayname: string;
}

export interface Registration {
  id: string;
  applicationId: string;
  roles: (Role.SELLER | Role.BUYER)[];
}

export interface User {
  id: string;
  email: string;
  active: boolean;
  username: string;
  data?: UserData;
  registrations: Registration[];
}

export interface Profile {
  id: string;
  user_name: string;
  address?: string;
  country: string;
  referral: string;
  referrer?: string;
  type: string;
  displayname?: string;
  email?: string;
}

export enum Role {
  SELLER = "seller",
  BUYER = "buyer",
}

export interface Device {
  id: string;
  device_id: string;
  name: string;
  device_ip: string;
  country_code: string;
  user_id: number;
  connected: boolean;
  final_score: number;
  createdAt: string;
  lastConnected: string;
  totalTodayUptime: number;
}

export interface DeviceData {
  device: Device;
  dataSold: number;
  sellTx: number;
  color: string;
}

export interface Settings {
  id: number;
  user_id: string;
  language: string;
  pause_proxy_expiry?: Date;
  device_id: string;
  createdAt: string;
  updatedAt: string;
}

export interface UserScore {
  lifetimeEarnings: number;
}

export interface Epoch {
  epoch_name: string;
  epoch_number: number;
  start_date: string;
  end_date: string;
}

export interface EpochEarning extends Epoch {
  totalCumulativePoints: number;
  totalCumulativeUptime: number;
  totalCumulativeReferralPoints: number;
  isCurrentEpoch: boolean;
}
